<template>
  <aside 
    ref="sidebar"
    @mouseenter="expandSidebar" 
    @mouseleave="collapseSidebar"
    :class="{ 'sidebar-collapsed': !isExpanded }"
    class="sidebar-container"
  >
    <nav>
      <router-link to="/home/blog">博客目录</router-link>
      <router-link to="/home/resource">资源分类</router-link>
      <router-link to="/home/aboutMe">个人信息</router-link>
      <router-link to="/home/test">测试页面</router-link>
    </nav>
  </aside>
</template>

<script>
export default {
  name: 'HomeSidebar',
  data() {
    return {
      isExpanded: true
    }
  },
  mounted() {
    window.addEventListener('mousemove', this.handleMouseMove)
    this.$nextTick(() => {
      this.emitSidebarWidth()
    })
  },
  beforeUnmount() {
    window.removeEventListener('mousemove', this.handleMouseMove)
  },
  methods: {
    handleMouseMove(event) {
      if (event.clientX < window.innerWidth / 10) {
        this.expandSidebar()
      }
    },
    expandSidebar() {
      this.isExpanded = true
      this.emitSidebarWidth()
    },
    collapseSidebar() {
      this.isExpanded = false
      this.emitSidebarWidth()
    },
    emitSidebarWidth() {
      const width = this.isExpanded ? 250 : 0
      this.$emit('sidebar-width-change', width)
    }
  }
}
</script>

<style scoped>
.sidebar-container {
  background-color: #f9f9f9;
  padding: 20px;
  width: 250px;
  transition: all 0.3s ease;
  position: relative; 
  top: 0;
}

.sidebar-collapsed {
  width: 0;
  padding: 20px 0;
  overflow: hidden;
}

nav {
  display: flex;
  flex-direction: column;
  gap: 10px;
  white-space: nowrap;
}

nav a {
  text-decoration: none;
  color: #333;
  padding: 10px;
  border-radius: 5px;
  opacity: 1;
  transition: opacity 0.3s ease;
}

.sidebar-collapsed nav a {
  opacity: 0;
}

nav a:hover, nav a.router-link-active {
  background-color: #e0e0e0;
}
</style>