<template>
  <div class="home-page-layout" ref="layout">
    <HomeHeader class="header"/>
    <div class="main-container">
      <HomeSidebar 
        class="sidebar" 
        @sidebar-width-change="updateSidebarWidth"
      />
      <div class="main-content">
        <router-view></router-view>
      </div>
    </div>
    <HomeFooter class="footer"/>
  </div>
</template>

<script>
import HomeHeader from './HomeHeader.vue'
import HomeFooter from './HomeFooter.vue'
import HomeSidebar from './HomeSidebar.vue'

export default {
  name: 'HomePageLayout',
  components: {
    HomeHeader,
    HomeFooter,
    HomeSidebar
  },
  methods: {
    updateSidebarWidth(width) {
      this.$refs.layout.style.setProperty('--sidebar-width', `${width}px`)
    }
  }
}
</script>

<style scoped>
:root {
  --sidebar-width: 250px;
}

.home-page-layout {
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
}

.main-container {
  display: grid;
  grid-template-columns: var(--sidebar-width) 1fr;
  transition: grid-template-columns 0.3s ease;
  position: relative;
}

.header {
  grid-row: 1;
  grid-column: 1 / -1;
  z-index: 20;
}

.sidebar {
  grid-column: 1;
  grid-row: 1;
  z-index: 10;
}

.main-content {
  grid-column: 2;
  padding: 20px;
  z-index: 1;
}

.footer {
  grid-row: 3;
  grid-column: 1 / -1;
}
</style>